<template>
	<div>
		<v-container fluid>
			<v-layout row>
				<v-flex xs12 sm12 md12 lg12 xl9>
					<v-stepper v-model="step">

						<v-stepper-header>
							<v-stepper-step :complete="step > 1" step="1">Personal Information</v-stepper-step>
							<v-divider></v-divider>
							<v-stepper-step :complete="step > 2" step="2">Extra Information</v-stepper-step>
						</v-stepper-header>

						<v-stepper-items>
							<v-stepper-content step="1">
								<v-card>
									<PersonalInformation @personalInfoValid="val => step = val ? 2 : 1" ref="personalInfo" :data="personal_information" :flat="true" />
									<v-card-actions>
										<v-spacer></v-spacer>
										<v-btn color="primary" @click="$refs.personalInfo.validateFields()">Next</v-btn>
									</v-card-actions>
								</v-card>
							</v-stepper-content>

							<v-stepper-content step="2">
								<v-card>
									<ExtraInformation @extraInfoValid="val => val ? save() : null" ref="extraInfo" :data="extra_information" :flat="true" :idNumberField="true"/>
									<v-card-actions>
										<v-spacer></v-spacer>
										<v-btn color="primary" @click="step = 1">Back</v-btn>
										<v-btn color="primary" @click="$refs.extraInfo.validateFields()">Save</v-btn>
									</v-card-actions>
								</v-card>
							</v-stepper-content>
						</v-stepper-items>

					</v-stepper>
				</v-flex>
			</v-layout>
		</v-container>

		<LoaderDialog :loaderDialog="loaderDialog"/>
		<vue-snotify></vue-snotify>
	</div>
</template>

<script>
	import PersonalInformation from '@/components/PersonalInformation';
	import ExtraInformation from '@/components/ExtraInformation';
	import LoaderDialog from '@/components/LoaderDialog';
  var generator = require('generate-password');
	export default {
		data: () => ({
			step: 1,
			personal_information: {},
			extra_information: {},
			loaderDialog: false
		}),
		components: {
			PersonalInformation,
			ExtraInformation,
			LoaderDialog
		},
		methods: {
			async save () {
        let password = generator.generate({
          length: 10,
          numbers: true,
          excludeSimilarCharacters: true,
          symbols: true
        });
				this.loaderDialog = true;
				try {
					const extra_information = JSON.parse(JSON.stringify(this.extra_information));
					delete extra_information.division.sections;

					try {
						const res = await this.$store.dispatch('account/CHECK_ID_NUMBER', extra_information.emp_id);
						console.log('Search result', res);
						if (res.exists) {
							this.$snotify.error('ID Number already exists!');
							this.loaderDialog = false;
							return;
						}
					} catch (error) {
						console.log(error);
						this.$snotify.error('An error occurred');
						this.loaderDialog = false;
						return;
					}

          // let password = extra_information.emp_id;
          //   if (password.length <= 5) {
          //     password = `mirdc${password}`;
          //   }
					const employee = {
						email: `${extra_information.emp_id}@mail.com`,
						password: password,
						emp_id: extra_information.emp_id,
						personal_information: this.personal_information,
						extra_information: extra_information,
					}
					await this.$store.dispatch('account/SAVE_EMPLOYEE', employee);
					this.personal_information = {};
					this.extra_information = {};
					this.$refs.personalInfo.resetValidation();
					this.$refs.extraInfo.resetValidation();
					this.step = 1;
          // Sending email notification
          await this.sendPasswordEmail(employee);
          this.$snotify.success('New user has been successfully added!');
				} catch (error) {
					console.log(error);
					this.$snotify.error('An error occurred');
				}
				this.loaderDialog = false;
			},
      async sendPasswordEmail(employee) {
        console.log("employee", employee)
        const emailEndpoint = 'http://10.10.120.43:3232/sendMail';
        const emailData = {
          to: employee.personal_information.email,
          from: 'PMD-MIS',
          system: 'SSO',
          subject: 'Your SSO Password',
          message: `<!DOCTYPE html>
            <html lang="en">
            <head>
              <meta charset="UTF-8">
              <meta http-equiv="X-UA-Compatible" content="IE=edge">
              <meta name="viewport" content="width=device-width, initial-scale=1.0">
              <style>
                body {
                  font-family: 'Arial', sans-serif;
                  color: #333;
                }
                .container {
                  max-width: 600px;
                  margin: 20px auto;
                  padding: 20px;
                  border: 1px solid #ddd;
                  border-radius: 10px;
                  background-color: #f9f9f9;
                }
                .button {
                  display: inline-block;
                  padding: 10px 20px;
                  background-color: #1976d2;
                  color: #000000;
                  text-decoration: none;
                  border-radius: 5px;
                }
              </style>
            </head>
            <body>
              <div class="container">
                <h2>Hello ${employee.personal_information.first_name} ${employee.personal_information.surname},</h2>
                <p>Your SSO password is: <strong>${employee.password}</strong></p>
                <p>Please keep this password confidential and do not share it with anyone.</p>
                <p>Click the button below to log in:</p>
                <a class="button" href="http://10.10.120.32:3536">Log In</a>
                <p>Thank you,<br>PMD-MIS</p>
              </div>
            </body>
            </html>`,
        };

        try {
          const response = await fetch(emailEndpoint, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(emailData),
          });

          if (!response.ok) {
            throw new Error(`Failed to send password email. Status: ${response.status}`);
          }

          console.log('Password email sent successfully');
        } catch (error) {
          console.error('Error sending password email:', error);
          throw error;
        }
      }
		}
	}
</script>



